$green : #8cc63f;

.card-list{
  display: flex;
  flex-wrap: wrap;

  .card-item{
    flex: 0 1 33.33333%;
    width: 33.33333%;
    padding: 0 15px;

    .card-image__link{
      height: auto;
      display: block;
      img{
        width: 100%;
        max-height: 190px;
      }
    }
    .card-content__title{
      background-color: $green;
      padding: 10px;
      color: #fff;
      margin: 0;
    }
    .card-content__detail{
      display: flex;
    }
  }
}



