@import './base.scss';

ul,li{
  list-style: none;
  margin: 0
}
a{
  text-decoration: none;
}
header{
  background-color: rgba(255, 255, 255, 0.35);
  -webkit-box-shadow: 0px 0px 25px 5px rgba(0, 0, 0, 0.15);
  -moz-box-shadow: 0px 0px 25px 5px rgba(0, 0, 0, 0.15);
  box-shadow: 0px 0px 25px 5px rgba(0, 0, 0, 0.15);
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.85) 0%, rgba(255, 255, 255, 0.3) 70%);
  background-repeat: repeat-x;
}

.navigation-menu{
  display: flex;
  height: 90px;
  justify-content: flex-end;
  align-items: center;

 .logo{
    margin-right: auto;
  }
}

.menu-item.quote-button:after{
  content: '' !important;
}

.menu-item__footer,
.menu-item.menu-mobile.last-child
{
  display: none;
}

.menu-item{
  position: relative;
  padding: 0 32px;

  &:after {
    content: '.';
    font-size: 60px;
    color: #8cc63f;
    position: absolute;
    top: -40px;
    right: -5px;
  }
}

.btn-style {
    background-color: #8cc63f;
    padding: 10px 20px;
    border-radius: 999px;
    color: #fff;
    text-transform: uppercase;
    font-weight: 500;
}

.btn-link{
  color: #603813;
  text-transform: uppercase;

  &:hover {
    text-decoration: none;
    color: #ca7628;
  }
}


