$green : #8cc63f;
$visit : #603813;

footer{
  color: #fff;
  background-color: $green;

  a{
    color: #fff;

    &:hover{
      color: $visit;
    }
  }
}

.footer-wrap{
  display: flex;
  justify-content: center;
  padding: 1rem 0;

  .footer-item{
    padding: 0 10px;

    &:not(:last-child){
      &:after{
        content: '|';
        color: #fff;
        padding-left: 20px;
      }
    }
  }
}

.footer__social-list{
  display: flex;
  justify-content: center;
}

.copyright{
  text-align: center;
  padding: 7px 0;
  margin: 0;
}


