.arrow_box {
  position: relative;
  border: 1px solid #ddd;
}
.arrow_box:after, .arrow_box:before {
  bottom: 100%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.arrow_box:after {
  border-bottom-color: #fff;
  border-width: 8px;
  margin-left: 54px;
}
.arrow_box:before {
  border-bottom-color: #ddd;
  border-width: 10px;
  margin-left: 52px;
}

